<template>
<div class="about">
 <div class="ab-top" >
   <div class="top-text" >
     <div class="top-left" >
       <div class="img-bor" >
          <img src="../assets/img/pic-guanywm.png@2x.png" alt=""  @click="jump" >
       </div>
        
     </div>
     <div class="top-left" >
      <div v-if="names" ><span @click="tologo" ></span>&nbsp/&nbsp<span @click="toenroll" ></span></div> 
        <el-dropdown  v-else trigger="click"   >
          <span class="el-dropdown-link">
            {{name}}<i class="el-icon-arrow-down el-icon-caret-bottom"></i>
          </span>
          <el-dropdown-menu slot="dropdown" style="z-index:2050" >
            <el-dropdown-item @click.native="user()" ></el-dropdown-item>
            <el-dropdown-item @click.native="tocol()" ></el-dropdown-item>
            <el-dropdown-item @click.native="amend()" ></el-dropdown-item>
            <el-dropdown-item @click.native="order()" ></el-dropdown-item>
            <el-dropdown-item @click.native="quit()"  ></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
     </div>
   </div>
 </div>
  <div class="we-content">

     <div class="we-title" >
       <div class="ab-china left ">
         
       </div>
       Comfany Profile
     </div>

     <div class="ab-com">
       <div class="com-img left" >
          <img src="../assets/img/pic-qiyejj.png@2x.png" alt="">
       </div>
       <div class="com-text right" >2017
         
         <span style="fontSize:18px;fontWeight:550" >,</span>
         
       </div>
     </div> 

     <div class="we-title" >
       <div class="ab-china left ">
         
       </div>
       CORE PRODUCTS SERVICES
     </div>

     <div class="ab-core size" >
       <div>
       </div>
       <div class="we-pro m-top">
         <div ><span style="fontSize:16px;fontWeight: 550" ></span></div>
         <div class="m-top">1
         </div>
         <div class="m-top">2
         </div>
         <div class="m-top">3
         </div>
         <div class="m-top">4)
         </div>
         <div class="m-top">5
         </div>
          <div class="m-top">61 2+3  4
         </div>
          <div class="m-top">7
         </div>
       </div>
     </div>

      <div class="we-title" >
       <div class="ab-china left ">
         
       </div>
       OFFICE ENVIRONMENT
     </div>

     <div class="ab-core size">
      <div></div>
      <div class="img-m">
        <img  src="../assets/img/pic-1@2x.png">
        <img  src="../assets/img/pic-2@2x.png">
        <img  src="../assets/img/pic-3@2x.png">
      </div>
     </div>

      <div class="we-title" >
       <div class="ab-china left ">
         
       </div>
       EMPLOYEE LIFE
     </div>

     <div class="ab-core size">
       <div></div>
     </div>
      <div class="left-img">
         <img src="../assets/img/pic-5@2x.png" alt="">
       </div>

  </div>
</div>
</template>
<script>
export default {
  data () {
    return {
      names:true
    }
  },
  methods: {
    jump() {
      this.$router.push('/')
    },
    order() {
      this.$router.push('/user/order')
    },
    tologo() {
      this.$router.push('/logo')
    },
    toenroll() {
      this.$router.push('/enroll')
    },
     judges() {
       if(sessionStorage.getItem('xtoken') || localStorage.getItem('Xtoken')) {
        this.name = localStorage.getItem('Bname')
        this.names = false
      } else {     
        this.names = true
      }
    },
    user() {
      this.$router.push('/user')
    },
    tocol() {
      this.$router.push('/user/fcoll')
    },
    amend() {
      this.$router.push('/user/root')
    },
     quit() {
        sessionStorage.removeItem('xtoken')
        localStorage.removeItem('Bname')
        localStorage.removeItem('Xtoken')
        localStorage.removeItem('valid')
        localStorage.removeItem('permissions')
          this.$router.replace({
           path: '/home',
         
          })
    },
  },
  created () {
    this.judges()
  },
  components: {
  }
}
</script>
<style lang="less" scoped>
.about {
  .ab-top {
    // height: 40px;
    background-color: #FE6603;
    .top-text {
      width: 1020px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      height: 40px;
      .top-left {
        cursor: pointer;
        height: 40px;
        display: flex;
        align-items: center;
        color: #fff;
        font-size: 12px;
        .img-bor {
          cursor: pointer;
          height: 24px;
          border-right: 1px solid #fff;
          padding-right: 10px;
          margin-right: 10px;
        }
      }
    }
  }
  .we-content {
    width: 1020px;
    margin: 0 auto 200px;
    padding-top: 20px;
    background-color: #fff;
    .we-title {
      border-bottom: 1px solid #F2F2F2;
      height: 40px ;
      line-height: 40px;
      padding: 0 14px;
      font-size: 12px;
      color:#999;
      margin-bottom: 1px;
      .ab-china {
        height: 40px;
        line-height: 40px;
        border-bottom: 2px solid #FE6603;
        font-size: 16px;
        color:#FE6603;
        margin-right: 10px;
        font-weight: 550;
      }
    }
    .ab-com {
      padding: 12px 14px 0;
      overflow: hidden;
      .com-img {
        width: 215px;
        height: 180px;
        overflow: hidden;
        // margin-right: 16px;
        img {
          width: 100%;
        }
      }
      .com-text {
        width: 760px;
        font-size: 14px;
        color:#333;
        line-height: 28px;
      }
    }
    .ab-core {
      padding: 24px 14px;
      .we-pro {
        margin-left: 16px;
      }
      .m-top {
        margin-top: 28px;
      } 
      .img-m {
        padding: 40px 26px 0 38px; 
        img {
          width: 915px;
          margin-bottom: 15px;
        }
      }
    }
    .size {
      font-size: 14px;
      line-height: 24px;
      color:#333;
    }
    .left-img {
      width: 1020px;
      margin-top: 20px;
      img {
        width: 100%;
      }
    }
  }
}
</style>



